import request from "../utils/request"; // 获取用户列表

export var getAllUserInfo = function getAllUserInfo(query) {
  return request({
    url: 'get_all_user',
    method: 'get',
    params: query
  });
}; // 删除用户

export var deleteUser = function deleteUser(query) {
  return request({
    url: 'del_user',
    method: 'get',
    params: query
  });
};