import { getAllUserInfo, deleteUser } from "../../api/user";
export default {
  name: 'UserManagement',
  data: function data() {
    return {
      query: {
        address: '',
        name: '',
        pageIndex: 1,
        pageSize: 10
      },
      tableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      pageTotal: 0,
      form: {},
      idx: -1,
      id: -1
    };
  },
  created: function created() {
    this.getData();
  },
  methods: {
    getData: function getData() {
      var _this = this;

      getAllUserInfo({
        name: this.query.name,
        page: this.query.pageIndex
      }).then(function (res) {
        // console.log('aaaaaaaaa', res.data);
        _this.tableData = res.data;
        _this.pageTotal = res.total;
      });
    },
    // 触发搜索按钮
    handleSearch: function handleSearch() {
      this.$set(this.query, 'pageIndex', 1);
      this.getData();
    },
    // 删除操作
    handleDelete: function handleDelete(index, row) {
      var _this2 = this;

      console.log(row.open_id); // 二次确认删除

      this.$confirm('确定要删除吗？', '提示', {
        type: 'warning'
      }).then(function () {
        deleteUser({
          open_id: row.open_id
        }).then(function (res) {
          if (res.code == 200) {
            _this2.$message.success('删除成功');

            _this2.tableData.splice(index, 1);
          }
        });
      }).catch(function () {
        _this2.$message.error('删除失败');
      });
    },
    // 多选操作
    handleSelectionChange: function handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // delAllSelection() {
    //     const length = this.multipleSelection.length;
    //     let str = '';
    //     this.delList = this.delList.concat(this.multipleSelection);
    //     for (let i = 0; i < length; i++) {
    //         str += this.multipleSelection[i].name + ' ';
    //     }
    //     this.$message.error(`删除了${str}`);
    //     this.multipleSelection = [];
    // },
    // 编辑操作
    // handleEdit(index, row) {
    //     this.idx = index;
    //     this.form = row;
    //     this.editVisible = true;
    // },
    // 保存编辑
    saveEdit: function saveEdit() {
      this.editVisible = false;
      this.$message.success("\u4FEE\u6539\u7B2C ".concat(this.idx + 1, " \u884C\u6210\u529F"));
      this.$set(this.tableData, this.idx, this.form);
    },
    // 分页导航
    handlePageChange: function handlePageChange(val) {
      this.$set(this.query, 'pageIndex', val);
      console.log('val', this.query);
      this.getData();
    }
  }
};